import useGetTokenSource from '@cetus/hooks/src/useGetTokenSource'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import useTokenSelectStore from '@cetus/stores/src/useTokenSelectStore'
import { Token } from '@cetus/types'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble, d, symbolDataDisplayProcessing, toLongCoinType } from '@cetus/utils'
import { Box, Button, Center, Flex, Text } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import CetusTooltip from '../CetusTooltip'
import SingleTokenInfo from '../SingleTokenInfo'
import BalanceValue from './BalanceValue'
import TokenGoplusModal from './TokenGoplusModal'

type TokenItemProps = {
  token: Token
  onSelectToken: (tokenInfo: Token) => void
  isDisableSelectTokenItem: boolean
  currentTab: string
  delImportTokenList: (tokens: Token[]) => void
  addUserCollectToken: (tokens: Token) => void
  delUserCollectToken: (tokens: Token) => void
  userCollectObj: Record<string, Token>
  userCollectList: any
  isShowCollectListBox: boolean
  isShowTokenListTab: boolean
  isShowCoinInfo: boolean
  onGoplusTokenClick?: (tokenInfo: Token) => void
  onClose: () => void
  isDisabled?: boolean
  isNotAllowClick?: boolean
  isHideDelIcon?: boolean
  isShowOperate?: boolean
  isShowTokenName?: boolean
  isChecked?: boolean
}

const TokenItem = ({
  token,
  onSelectToken,
  isDisableSelectTokenItem,
  currentTab,
  delImportTokenList,
  addUserCollectToken,
  delUserCollectToken,
  userCollectObj,
  userCollectList,
  isShowCollectListBox,
  isShowOperate = true,
  isShowCoinInfo = true,
  isShowTokenListTab,
  onGoplusTokenClick,
  onClose,
  isNotAllowClick = false,
  isHideDelIcon = false,
  isDisabled = false,
  isShowTokenName = true,
  isChecked = false
}: TokenItemProps) => {
  const { isShowImportBtn, setTokenModalOpacity } = useTokenSelectStore()
  // const [canCollect, setCanCollect] = useState(true)
  // useEffect(() => {
  //   console.log('üöÄüöÄüöÄ ~ userCollectObj:', userCollectObj)
  //   if (
  //     !(
  //       userCollectObj &&
  //       userCollectObj[token.coin_type] &&
  //       userCollectObj[token.coin_type].suggested_priority &&
  //       userCollectObj[token.coin_type].suggested_priority[0]
  //     )
  //   ) {
  //     setCanCollect(true)
  //   } else {
  //     setCanCollect(false)
  //   }
  // }, [userCollectObj, token])

  const { currentAccount } = useAccountStore()

  const { getTokenSource, getTrustTokenSource } = useGetTokenSource()
  const [isShowWarningIcon, setIsShowWarningIcon] = useState(false)
  useEffect(() => {
    if (token) {
      const tokenSource = getTokenSource([token])
      if (tokenSource && tokenSource.length > 0) setIsShowWarningIcon(true)
    }
  }, [token])
  const [isShowGoplusModal, setIsShowGoplusModal] = useState(false)
  const { isApp } = useWindowWidth()
  return (
    <Button
      variant="unstyled"
      display="flex"
      w="100%"
      minH="64px"
      justifyContent="space-between"
      alignItems="center"
      cursor={(isChecked && isDisableSelectTokenItem) || isNotAllowClick || isDisabled ? 'not-allowed' : 'pointer'}
      userSelect="none"
      p="0 16px"
      marginTop="8px"
      bg={isChecked ? 'primary_opacity.10' : 'none'}
      border="1px solid"
      borderColor={isChecked ? 'primary_opacity.50' : 'transparent'}
      borderRadius="14px"
      _hover={
        isDisabled
          ? {}
          : {
              bg: 'primary_opacity.10',
              borderRadius: '14px'
            }
      }
      opacity={(isChecked && isDisableSelectTokenItem) || isDisabled ? '0.5' : '1'}
      onClick={() => {
        if (isDisabled) return
        if (!isNotAllowClick) {
          onSelectToken(token)
        }
      }}
    >
      <SingleTokenInfo
        symbolFontWeight="600"
        token={token}
        haveAddress={true}
        imgBoxStyle={{ w: '32px', h: '32px' }}
        symbolFontSize="14px"
        symbolEllipsesDecimals={20}
        nameEllipsesDecimals={28}
        haveName={isShowTokenName}
        warningIcon={{ isNeedShow: currentTab !== 'Imported' && isShowWarningIcon }}
      />
      <Flex alignItems="center">
        {currentAccount?.address && (
          <Box textAlign="right" mr="4px">
            <BalanceValue tokenInfo={token} />
            {token?.balanceUSD !== undefined && d(token?.balanceUSD).gt(0) && (
              <Text mt="6px" fontSize="12px">
                {symbolDataDisplayProcessing(token?.balanceUSD)}
              </Text>
            )}
          </Box>
        )}
        {isShowOperate && (
          <>
            {/* {currentTab == 'Imported' && !isShowImportBtn ? ( */}
            {!isHideDelIcon && !(token?.is_verified === undefined ? token?.is_trusted : token?.is_verified) ? (
              <CetusTooltip showTooltip={isApp ? false : true} placement="top" tooltip={<Text fontSize="12px">Remove</Text>}>
                <Center>
                  <Icon
                    marginLeft="2px"
                    xlinkHref="#icon-icon_del"
                    svgHover="primary"
                    onClick={e => {
                      e.stopPropagation()
                      delImportTokenList([token])
                    }}
                  />
                </Center>
              </CetusTooltip>
            ) : null}

            {userCollectObj[toLongCoinType(token.coin_type)] && isShowCollectListBox ? (
              <CetusTooltip showTooltip={isApp ? false : true} placement="top" tooltip={<Text fontSize="12px">Remove to Watchlist</Text>}>
                <Center>
                  <Icon
                    marginLeft="4px"
                    xlinkHref="#icon-icon_star_sel"
                    svgFill="primary"
                    svgHover="primary"
                    // cursor={canCollect ? 'pointer' : 'no-drop'}
                    onClick={e => {
                      e.stopPropagation()
                      // if (!canCollect) {
                      //   return false
                      // }
                      delUserCollectToken(token)
                    }}
                  />
                </Center>
              </CetusTooltip>
            ) : !isShowImportBtn && isShowCollectListBox ? (
              <CetusTooltip showTooltip={isApp ? false : true} placement="top" tooltip={<Text fontSize="12px">Add to Watchlist</Text>}>
                <Center>
                  <Icon
                    marginLeft="4px"
                    xlinkHref="#icon-icon_star"
                    variant="primary"
                    cursor={userCollectList?.length >= 100 ? 'not-allowed' : 'pointer'}
                    onClick={e => {
                      e.stopPropagation()
                      if (userCollectList?.length < 100) {
                        addUserCollectToken(token)
                      }
                    }}
                  />
                </Center>
              </CetusTooltip>
            ) : null}
            {isShowCoinInfo && (
              <CetusTooltip
                placement="top"
                tooltip={
                  <Text fontSize="12px" color="text_caption">
                    Coin Info
                  </Text>
                }
              >
                <Center
                  as="button"
                  ml="4px"
                  onClick={(e: any) => {
                    cancelBubble(e)
                    if (onGoplusTokenClick) {
                      onGoplusTokenClick(token)
                    } else {
                      e.target.blur()
                      setIsShowGoplusModal(true)
                      setTokenModalOpacity(0)
                    }
                  }}
                >
                  <Icon xlinkHref="#icon-icon_tips" />
                </Center>
              </CetusTooltip>
            )}
          </>
        )}
      </Flex>
      {isShowGoplusModal && (
        <TokenGoplusModal
          tokenInfo={token}
          isOpen={isShowGoplusModal}
          onBack={() => {
            setIsShowGoplusModal(false)
            setTokenModalOpacity(1)
          }}
          onClose={onClose}
        />
      )}
    </Button>
  )
}

export default TokenItem
