import { Token } from '@cetus/types'
import { useVirtualizer } from '@tanstack/react-virtual'
import { useRef } from 'react'
import TokenItem from './TokenItem'

// import { List } from 'react-virtualized'

type TokenListVirtualizedProps = {
  list: (Token & { isDisabled?: boolean })[]
  onSelectToken: (tokenInfo: Token) => void
  isDisableSelectTokenItem: boolean
  value: string | string[] | Token | Token[]
  currentTab: string
  delImportTokenList: (tokens: Token[]) => void
  addUserCollectToken: (tokens: Token) => void
  delUserCollectToken: (tokens: Token) => void
  userCollectObj: Record<string, Token>
  userCollectList: any
  width: number
  height?: string
  isShowCollectListBox: boolean
  isShowOperate?: boolean
  isShowTokenListTab: boolean
  isShowCoinInfo: boolean
  onClose: () => void
  onGoplusTokenClick?: (tokenInfo: Token) => void
  isShowTokenName?: boolean
  notAllowedSelect?: (token: Token) => boolean
}

const TokenListVirtualized = ({
  list,
  onSelectToken,
  isDisableSelectTokenItem,
  value,
  currentTab,
  delImportTokenList,
  addUserCollectToken,
  delUserCollectToken,
  userCollectObj,
  userCollectList,
  width,
  height,
  isShowCollectListBox,
  isShowOperate = true,
  isShowTokenListTab,
  onClose,
  onGoplusTokenClick,
  isShowTokenName = true,
  notAllowedSelect,
  isShowCoinInfo
}: TokenListVirtualizedProps) => {
  const parentRef = useRef<HTMLDivElement>(null)
  const rowVirtualizer = useVirtualizer({
    count: list.length, // ÊÄªÈ°πÊï∞
    getScrollElement: () => parentRef.current, // Ëé∑ÂèñÊªöÂä®ÂÆπÂô®ÁöÑÂÖÉÁ¥†
    estimateSize: () => (Array.isArray(value) ? 72 : 64), // ÊØèÈ°πÁöÑÈªòËÆ§È´òÂ∫¶Ôºà‰º∞ÁÆóÂÄºÔºâ
    overscan: 10 // È¢ÑÂÖàÊ∏≤ÊüìÁöÑÈ°πÊï∞ÔºàÊèêÈ´òÊªöÂä®ÊµÅÁïÖÊÄßÔºâ
  })

  return (
    <div
      ref={parentRef}
      style={{
        minHeight: '100px',
        height: height ? height : '400px', // ÂàóË°®ÁöÑÂèØËßÅÂå∫ÂüüÈ´òÂ∫¶
        overflowY: 'auto' // ÂêØÁî®ÂûÇÁõ¥ÊªöÂä®
      }}
    >
      <div
        style={{
          height: `${rowVirtualizer.getTotalSize()}px`, // ËÆ°ÁÆóÊâÄÊúâÈ°πÁõÆÁöÑÊÄªÈ´òÂ∫¶
          position: 'relative' // ‰ΩøÁî®ÁªùÂØπÂÆö‰Ωç
        }}
      >
        {rowVirtualizer.getVirtualItems().map(virtualRow => {
          const token = list[virtualRow.index]
          return (
            <div
              key={token.coin_type}
              style={{
                position: 'absolute',
                top: virtualRow.start,
                left: 0,
                width: '100%',
                height: virtualRow.size,
                marginBottom: Array.isArray(value) ? '8px' : '0px'
                // borderBottom: '1px solid #ccc',
                // display: 'flex',
                // alignItems: 'center',
                // padding: '10px',
                // backgroundColor: virtualRow.index % 2 === 0 ? '#f4f4f4' : '#fff'
              }}
            >
              {/* Row {virtualRow.index} */}
              <TokenItem
                onGoplusTokenClick={onGoplusTokenClick}
                onClose={onClose}
                token={token}
                onSelectToken={onSelectToken}
                isNotAllowClick={notAllowedSelect?.(token)}
                isDisableSelectTokenItem={isDisableSelectTokenItem}
                isChecked={
                  typeof value === 'string'
                    ? value === token?.coin_type
                    : Array.isArray(value)
                      ? typeof value?.[0] === 'string'
                        ? value?.includes(token?.coin_type)
                        : value?.some(item => item?.coin_type === token?.coin_type)
                      : value?.coin_type === token?.coin_type
                }
                currentTab={currentTab}
                delImportTokenList={delImportTokenList}
                addUserCollectToken={addUserCollectToken}
                delUserCollectToken={delUserCollectToken}
                isShowCollectListBox={isShowCollectListBox}
                isShowCoinInfo={isShowCoinInfo}
                isShowTokenListTab={isShowTokenListTab}
                userCollectObj={userCollectObj}
                isDisabled={list[virtualRow.index]?.isDisabled}
                userCollectList={userCollectList}
                isShowOperate={isShowOperate}
                isShowTokenName={isShowTokenName}
              />
            </div>
          )
        })}
      </div>
    </div>
    // <List
    //   width={width}
    //   height={height}
    //   rowHeight={64}
    //   rowCount={list.length}
    //   rowRenderer={({ index, key, style }) => (
    //     <div key={list[index].coin_type} style={style}>
    //       <TokenItem
    //         token={list[index]}
    //         onSelectToken={onSelectToken}
    //         isDisableSelectTokenItem={isDisableSelectTokenItem}
    //         lastSelectedToken={lastSelectedToken}
    //         currentTab={currentTab}
    //         delImportTokenList={delImportTokenList}
    //         addCollectToken={addCollectToken}
    //         delCollectToken={delCollectToken}
    //         userCollectObj={userCollectObj}
    //       />
    //     </div>
    //   )}
    // />
  )
}

export default TokenListVirtualized
